import { AnimatePresence, motion } from "framer-motion";
import { useState } from "react";
import { Link } from "react-router-dom";
import { useRecoilValue } from "recoil";
import { languageState, wordsSelector } from "../atoms";
import HeaderMenu from "../components/HeaderMenu";
import LanguageSetter from "../components/LanguageSetter";
import Word from "../components/Word";
import { NoWords } from "../styles/formStyle";
import { wordListVar } from "../styles/motionVariants";
import { ReviewContainer } from "../styles/screen/reviewStyle";

function Review() {
  const [langNum, setLangNum] = useState(0);
  const language = useRecoilValue(languageState);
  const words = useRecoilValue(wordsSelector);

  return (
    <>
      <HeaderMenu />
      <ReviewContainer>
        <LanguageSetter
          page={"review"}
          langNum={langNum}
          setLangNum={setLangNum}
        />
        <AnimatePresence exitBeforeEnter>
          <motion.ul
            key={language + "_wordList"}
            variants={wordListVar}
            initial="hidden"
            animate="show"
            exit="hide"
          >
            {words.length > 0 ? (
              words.map((word) => <Word key={String(word._id)} word={word} />)
            ) : (
              <NoWords>  .</NoWords>
            )}
          </motion.ul>
        </AnimatePresence>
        <Link to={"/"}>
          <button> </button>
        </Link>
      </ReviewContainer>
    </>
  );
}

export default Review;
