export const abbreviateLanguage = (language: string) => {
  const lang =
    language === "English"
      ? "En"
      : language === "Espaol"
      ? "Es"
      : language === "Franais"
      ? "Fr"
      : language === "Deutsch"
      ? "De"
      : language === ""
      ? "Jp"
      : language === ""
      ? "Ch"
      : "Ru";

  return lang;
};

export const getLanguageInKorean = (language: string) => {
  const languageInKorean =
    language === "English"
      ? ""
      : language === "Espaol"
      ? ""
      : language === "Franais"
      ? ""
      : language === "Deutsch"
      ? ""
      : language === ""
      ? ""
      : language === ""
      ? ""
      : "";
  return languageInKorean;
};
