import { useForm } from "react-hook-form";
import { useEffect, useState } from "react";
import { languages } from "../util/language";
import LanguageSetter from "./LanguageSetter";
import InputWithKeyboard from "./InputWithKeyboard";
import { ButtonContainer, Form } from "../styles/formStyle";
import { basicShowVariants } from "../styles/motionVariants";
import { DarkBox } from "../styles/boxStyle";
import TextareaWithKeyboard from "./TextareaWithKeyboard";
import {
  WordFormContainer,
  WordFormTPBox,
} from "../styles/screen/addWordStyle";

export interface IForm {
  spelling: string;
  pronunciation: string;
  meaning: string;
  collocation: string;
  association: string;
  ex: string;
  syn: string;
  ant: string;
}

export type inputNames =
  | "spelling"
  | "pronunciation"
  | "meaning"
  | "collocation"
  | "association"
  | "ex"
  | "syn"
  | "ant";

function WordForm() {
  const { register, handleSubmit, setValue, setFocus } = useForm<IForm>();
  const [numWords, setNumWords] = useState(0);
  const [langNum, setLangNum] = useState(0);

  useEffect(() => {
    setFocus("spelling");
  }, [setFocus]);

  const onValid = (data: IForm) => {
    const today = new Date();
    fetch("/api/words", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ language: languages[langNum], data, today }),
    });
    Object.keys(data).forEach((key) => setValue(key as inputNames, ""));
    setNumWords((prev) => prev + 1);
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });

    setFocus("spelling");
  };

  return (
    <>
      <WordFormContainer
        variants={basicShowVariants}
        initial="hidden"
        animate="show"
      >
        <Form onSubmit={handleSubmit(onValid)}>
          <WordFormTPBox>
            <h3>  : {numWords}</h3>
            <h3> </h3>
            <LanguageSetter
              page="addWords"
              langNum={langNum}
              setLangNum={setLangNum}
            />
          </WordFormTPBox>

          <ul>
            <DarkBox>
              <li>
                <label>
                  <strong>*</strong>
                  <span>
                        Spelling  ,  
                       .
                  </span>
                  <span>
                           .
                  </span>
                </label>
                <InputWithKeyboard
                  register={register}
                  language={languages[langNum]}
                  inputName="spelling"
                  placeholder="required"
                  isRequired={true}
                />
              </li>

              <li>
                <label>
                  <strong></strong>
                </label>
                <input {...register("pronunciation")} placeholder="optional" />
              </li>
              <li>
                <label>
                  <strong>*</strong>
                </label>
                <input
                  {...register("meaning", { required: true })}
                  placeholder="required"
                />
              </li>
            </DarkBox>
            <WordFormTPBox>
              <li>
                <label>
                  <strong></strong>
                  <span>
                          .
                  </span>
                  <span>
                    [ ]       
                        .
                  </span>
                  <span>(   10)</span>
                </label>
                <InputWithKeyboard
                  register={register}
                  language={languages[langNum]}
                  inputName="collocation"
                  placeholder="optional(, )"
                  isRequired={false}
                />
              </li>
            </WordFormTPBox>
            <DarkBox>
              <li>
                <label>
                  <strong> </strong>
                  <span>
                        , ,  ,  
                     , tmi .
                  </span>
                  <span>
                    Tip : , , , , 
                      .
                  </span>
                  <span>(   50)</span>
                </label>
                <input
                  {...register("association")}
                  placeholder="optional(, )"
                ></input>
              </li>
            </DarkBox>

            <DarkBox>
              <li>
                <label>
                  <strong></strong>
                  <span>
                           . 
                       .
                  </span>
                  <span>
                    [ ]     ,  
                       .
                  </span>
                  <span>(   20)</span>
                </label>
                <TextareaWithKeyboard
                  register={register}
                  language={languages[langNum]}
                  inputName="ex"
                  isRequired={false}
                  placeholder={"optional"}
                />
              </li>
            </DarkBox>
            <WordFormTPBox>
              <li>
                <label>
                  <strong></strong>
                  <span>(   10)</span>
                </label>
                <InputWithKeyboard
                  register={register}
                  language={languages[langNum]}
                  inputName="syn"
                  placeholder="optional(, )"
                  isRequired={false}
                  isUpper={true}
                />
              </li>

              <li>
                <label>
                  <strong></strong>
                  <span>(   10)</span>
                </label>
                <InputWithKeyboard
                  register={register}
                  language={languages[langNum]}
                  inputName="ant"
                  placeholder="optional(, )"
                  isRequired={false}
                  isUpper={true}
                />
              </li>
            </WordFormTPBox>
          </ul>
          <ButtonContainer>
            <button></button>
          </ButtonContainer>
        </Form>
      </WordFormContainer>
    </>
  );
}

export default WordForm;
