import styled from "styled-components";
import { useForm } from "react-hook-form";
import validator from "validator";
import { useNavigate } from "react-router-dom";
import { useRecoilState } from "recoil";
import { loginState } from "../atoms";
import { useEffect } from "react";
import { ButtonContainer, ErrorMessage } from "../styles/formStyle";
import {
  JoinFormContainer,
  JoinPageForm,
  JoinPwConfirm,
} from "../styles/screen/joinStyle";
import { basicShowVariants } from "../styles/motionVariants";
import { DarkBox } from "../styles/boxStyle";

interface IForm {
  email: string;
  name: string;
  password: string;
  passwordConfirm: string;
}

function JoinForm() {
  const {
    register,
    handleSubmit,
    setValue,
    formState: { errors },
    setError,
    setFocus,
  } = useForm<IForm>();
  const navigate = useNavigate();

  const [login, setLogin] = useRecoilState(loginState);
  if (login.loggedIn) {
    fetch("/api/users/logout");
    setLogin({
      loggedIn: false,
      user: null,
    });
  }

  useEffect(() => {
    setFocus("email");
  }, [setFocus]);

  const onValid = (data: IForm) => {
    if (
      !validator.isStrongPassword(data.password, {
        minUppercase: 0,
      })
    ) {
      setError("password", {
        message:
          " 8 ,  , ,    .",
      });
    } else if (data.password !== data.passwordConfirm) {
      setError("passwordConfirm", { message: "  ." });
    } else if (!validator.isEmail(data.email)) {
      setError("email", { message: "  ." });
    } else {
      fetch("/api/users/join", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ data }),
      }).then((response) => {
        const status = response.status;
        if (status === 400) {
          setError("email", { message: "  ." });
        } else {
          navigate("/login");
        }
      });
    }

    setValue("email", "");
    setValue("name", "");
    setValue("password", "");
    setValue("passwordConfirm", "");
  };
  return (
    <>
      <JoinFormContainer>
        <JoinPageForm
          onSubmit={handleSubmit(onValid)}
          variants={basicShowVariants}
          initial="hidden"
          animate="show"
          custom={{ yValue: -50 }}
        >
          <h3> </h3>
          <ul>
            <DarkBox>
              <li>
                <label>
                  <strong></strong>
                </label>
                <input
                  type={"email"}
                  {...register("email", {
                    required: "  .",
                  })}
                  placeholder="required"
                />
              </li>
              <ErrorMessage>
                {errors?.email?.message && (
                  <span>{errors?.email?.message}</span>
                )}
              </ErrorMessage>
            </DarkBox>
            <DarkBox>
              <li>
                <label>
                  <strong></strong>
                </label>
                <input
                  type={"text"}
                  {...register("name", {
                    required: " .",
                  })}
                  placeholder="required"
                />
              </li>
              <ErrorMessage>
                {errors?.name?.message && <span>{errors?.name?.message}</span>}
              </ErrorMessage>
            </DarkBox>
            <DarkBox>
              <li>
                <label>
                  <strong></strong>
                </label>
                <span>
                   8 ,  , ,  
                  .
                </span>
                <input
                  type={"password"}
                  {...register("password", {
                    required: " .",
                  })}
                  placeholder="required"
                />
              </li>
              <ErrorMessage>
                {errors?.password?.message && (
                  <span>{errors?.password?.message}</span>
                )}
              </ErrorMessage>
              <li>
                <label>
                  <JoinPwConfirm> </JoinPwConfirm>
                </label>
                <input
                  type={"password"}
                  {...register("passwordConfirm", {
                    required:
                      "       .",
                  })}
                  placeholder="required"
                />
              </li>
              <ErrorMessage>
                {errors?.passwordConfirm?.message && (
                  <span>{errors?.passwordConfirm?.message}</span>
                )}
              </ErrorMessage>
            </DarkBox>
          </ul>
          <ButtonContainer>
            <button></button>
          </ButtonContainer>
        </JoinPageForm>
      </JoinFormContainer>
    </>
  );
}

export default JoinForm;
